/* Witch-Themed Portfolio Styles */
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&family=Inter:wght@300;400;500;600&display=swap');

:root {
    /* Magical Color Palette */
    --lavender: #E6E6FA;
    --soft-lavender: #F0F0FF;
    --peach-pink: #FFB6C1;
    --soft-peach: #FFE4E1;
    --midnight-blue: #1a1a2e;
    --deep-purple: #16213e;
    --cream: #FDF6E3;
    --pearl-white: #F8F8FF;
    --magical-gold: #FFD700;
    --mystic-purple: #8A2BE2;
    
    /* Typography */
    --heading-font: 'Playfair Display', serif;
    --body-font: 'Inter', sans-serif;
    
    /* Shadows and Effects */
    --soft-shadow: 0 8px 32px rgba(138, 43, 226, 0.1);
    --glow-shadow: 0 0 20px rgba(255, 182, 193, 0.3);
    --magical-gradient: linear-gradient(135deg, var(--lavender), var(--peach-pink), var(--soft-lavender));
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: var(--body-font);
    background: linear-gradient(135deg, var(--midnight-blue), var(--deep-purple));
    color: var(--cream);
    line-height: 1.6;
    overflow-x: hidden;
    position: relative;
    font-size: 1.25rem;
}

/* Magical Background Elements */
body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: 
        radial-gradient(circle at 20% 80%, rgba(255, 182, 193, 0.1) 0%, transparent 50%),
        radial-gradient(circle at 80% 20%, rgba(230, 230, 250, 0.1) 0%, transparent 50%),
        radial-gradient(circle at 40% 40%, rgba(255, 214, 0, 0.05) 0%, transparent 50%);
    pointer-events: none;
    z-index: -1;
}

/* Floating Sparkles */
.sparkle {
    position: fixed;
    width: 4px;
    height: 4px;
    background: var(--magical-gold);
    border-radius: 50%;
    animation: sparkle 3s infinite;
    pointer-events: none;
    z-index: 1;
}

.sparkle:nth-child(1) { top: 10%; left: 10%; animation-delay: 0s; }
.sparkle:nth-child(2) { top: 20%; left: 80%; animation-delay: 0.5s; }
.sparkle:nth-child(3) { top: 60%; left: 20%; animation-delay: 1s; }
.sparkle:nth-child(4) { top: 80%; left: 70%; animation-delay: 1.5s; }
.sparkle:nth-child(5) { top: 40%; left: 90%; animation-delay: 2s; }

@keyframes sparkle {
    0%, 100% { opacity: 0; transform: scale(0); }
    50% { opacity: 1; transform: scale(1); }
}

/* Custom Cursor */
.custom-cursor {
    position: fixed;
    width: 20px;
    height: 20px;
    background: radial-gradient(circle, var(--peach-pink), transparent);
    border-radius: 50%;
    pointer-events: none;
    z-index: 9999;
    transition: transform 0.1s ease;
}

.cursor-trail {
    position: fixed;
    width: 8px;
    height: 8px;
    background: var(--lavender);
    border-radius: 50%;
    pointer-events: none;
    z-index: 9998;
    opacity: 0.6;
}

/* Navigation */
.navbar {
    background: rgba(26, 26, 46, 0.9);
    backdrop-filter: blur(10px);
    border-bottom: 1px solid rgba(255, 182, 193, 0.2);
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    transition: all 0.3s ease;
}

.nav-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-logo a {
    font-family: 'Montserrat', sans-serif;
    font-size: 2.2rem;
    font-weight: 700;
    color: var(--peach-pink);
    text-decoration: none;
    background: none;
    -webkit-background-clip: unset;
    -webkit-text-fill-color: unset;
    background-clip: unset;
    letter-spacing: 0.5px;
}

.nav-menu {
    display: flex;
    list-style: none;
    gap: 2rem;
}

.nav-link {
    color: var(--cream);
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
    position: relative;
}

.nav-link::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 0;
    height: 2px;
    background: var(--magical-gradient);
    transition: width 0.3s ease;
}

.nav-link:hover::after {
    width: 100%;
}

.nav-link:hover {
    color: var(--peach-pink);
    text-shadow: 0 0 10px rgba(255, 182, 193, 0.5);
}

/* Hero Section */
.hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    padding: 2rem 2rem;
    position: relative;
}

.hero-container {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3.5rem;
    align-items: center;
}

.hero-title {
    font-family: var(--heading-font);
    font-size: 4.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    background: linear-gradient(45deg, var(--peach-pink), var(--lavender), var(--magical-gold));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    animation: titleGlow 3s ease-in-out infinite alternate;
}

@keyframes titleGlow {
    0% { filter: drop-shadow(0 0 10px rgba(255, 182, 193, 0.3)); }
    100% { filter: drop-shadow(0 0 20px rgba(230, 230, 250, 0.5)); }
}

.highlight {
    color: var(--magical-gold);
    text-shadow: 0 0 15px rgba(255, 215, 0, 0.5);
}

.hero-subtitle {
    font-size: 2rem;
    color: var(--lavender);
    margin-bottom: 1rem;
    font-weight: 500;
}

.hero-description {
    font-size: 1.3rem;
    color: var(--cream);
    margin-bottom: 2rem;
    opacity: 0.9;
}

.hero-buttons {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

.btn {
    padding: 18px 36px;
    border: none;
    border-radius: 25px;
    font-weight: 600;
    text-decoration: none;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    font-size: 1.2rem;
}

.btn-primary {
    background: linear-gradient(45deg, var(--peach-pink), var(--lavender));
    color: var(--midnight-blue);
    box-shadow: var(--glow-shadow);
}

.btn-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(255, 182, 193, 0.4);
}

.btn-secondary {
    background: transparent;
    color: var(--cream);
    border: 2px solid var(--peach-pink);
}

.btn-secondary:hover {
    background: var(--peach-pink);
    color: var(--midnight-blue);
    transform: translateY(-2px);
}

/* Profile Card */
.profile-card {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(15px);
    border-radius: 20px;
    padding: 2.5rem;
    text-align: center;
    border: 1px solid rgba(255, 182, 193, 0.3);
    box-shadow: var(--soft-shadow);
    animation: float 6s ease-in-out infinite;
}

@keyframes float {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-20px); }
}

.profile-image {
    width: 200px;
    height: 200px;
    margin: 0 auto 1rem;
    background: linear-gradient(45deg, var(--lavender), var(--peach-pink));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4rem;
    color: var(--midnight-blue);
    box-shadow: var(--glow-shadow);
}

/* Sections */
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
}

.section-title {
    font-family: var(--heading-font);
    font-size: 3.2rem;
    text-align: center;
    margin-bottom: 3rem;
    background: linear-gradient(45deg, var(--peach-pink), var(--lavender));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

/* About Section */
.about {
    padding: 5rem 0;
    background: rgba(255, 255, 255, 0.02);
}

.about-content {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
}

.about-text p {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
    color: var(--cream);
    opacity: 0.9;
}

.about-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.stat {
    text-align: center;
    padding: 2.5rem;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 15px;
    border: 1px solid rgba(255, 182, 193, 0.2);
    transition: transform 0.3s ease;
}

.stat:hover {
    transform: translateY(-5px);
    box-shadow: var(--glow-shadow);
}

.stat h3 {
    font-size: 2.5rem;
    font-family: var(--heading-font);
    font-weight: 700;
    color: var(--magical-gold);
    margin-bottom: 0.5rem;
}

.stat p {
    font-size: 1.2rem;
    color: var(--lavender);
    font-weight: 500;
}

/* Skills Section */
.skills {
    padding: 5rem 0;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.skill-category {
    background: rgba(255, 255, 255, 0.05);
    padding: 2.5rem;
    border-radius: 20px;
    border: 1px solid rgba(255, 182, 193, 0.2);
    backdrop-filter: blur(10px);
    transition: transform 0.3s ease;
}

.skill-category:hover {
    transform: translateY(-5px);
    box-shadow: var(--glow-shadow);
}

.skill-category h3 {
    font-size: 2rem;
    font-family: var(--heading-font);
    font-weight: 700;
    color: var(--peach-pink);
    margin-bottom: 1.5rem;
    text-align: center;
}

.skill-items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 1rem;
}

.skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 10px;
    transition: all 0.3s ease;
}

.skill-item:hover {
    background: rgba(255, 182, 193, 0.1);
    transform: scale(1.05);
}

.skill-item i {
    font-size: 2rem;
    color: var(--magical-gold);
    margin-bottom: 0.5rem;
}

.skill-item span {
    color: var(--cream);
    font-weight: 500;
}

/* Projects Section */
.projects {
    padding: 5rem 0;
    background: rgba(255, 255, 255, 0.02);
}

.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
}

.project-card {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 20px;
    overflow: hidden;
    border: 1px solid rgba(255, 182, 193, 0.2);
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
}

.project-card:hover {
    transform: translateY(-10px);
    box-shadow: var(--glow-shadow);
}

.project-image {
    height: 200px;
    background: linear-gradient(45deg, var(--lavender), var(--peach-pink));
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    color: var(--midnight-blue);
}

.project-content {
    padding: 2rem;
}

.project-content h3 {
    font-size: 2rem;
    font-family: var(--heading-font);
    font-weight: 700;
    color: var(--peach-pink);
    margin-bottom: 1rem;
}

.project-content p {
    color: var(--cream);
    margin-bottom: 1.5rem;
    opacity: 0.9;
}

.project-tech {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
}

.project-tech span {
    background: rgba(255, 182, 193, 0.2);
    color: var(--lavender);
    padding: 0.3rem 0.8rem;
    border-radius: 15px;
    font-size: 0.9rem;
    font-weight: 500;
}

.project-links {
    display: flex;
    gap: 1rem;
}

.project-link {
    color: var(--magical-gold);
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease;
}

.project-link:hover {
    color: var(--peach-pink);
}

/* Contact Section */
.contact {
    padding: 5rem 0;
    min-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contact-content {
    display: grid;
    grid-template-columns: 1fr 350px;
    gap: 4rem;
    align-items: center;
    width: 100%;
    max-width: none;
    margin: 0;
    min-height: 400px;
}

.contact-left, .contact-right {
    width: 100%;
    height: 100%;
    justify-content: center;
}

.contact-left h3 {
    font-size: 2.5rem;
    font-family: var(--heading-font);
    font-weight: 700;
    color: var(--peach-pink);
    margin-bottom: 0.5rem;
    text-align: left;
}

.contact-left p {
    font-size: 1.3rem;
    color: var(--cream);
    margin-bottom: 1.2rem;
    opacity: 0.9;
    text-align: left;
    max-width: 350px;
}

.social-links {
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
    margin-top: 1.2rem;
}

.contact-right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    height: 100%;
}

.contact-details {
    width: 100%;
    max-width: 350px;
}

.contact-item {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1rem;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    border: 1px solid rgba(255, 182, 193, 0.2);
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
}

.contact-item i {
    color: var(--magical-gold);
    font-size: 1.2rem;
}

.contact-item span {
    color: var(--cream);
}

.social-link {
    width: 50px;
    height: 50px;
    background: linear-gradient(45deg, var(--peach-pink), var(--lavender));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--midnight-blue);
    text-decoration: none;
    font-size: 1.2rem;
    transition: all 0.3s ease;
}

.social-link:hover {
    transform: translateY(-3px);
    box-shadow: var(--glow-shadow);
}

/* Contact Form */
.contact-form {
    background: rgba(255, 255, 255, 0.05);
    padding: 2.5rem;
    border-radius: 20px;
    border: 1px solid rgba(255, 182, 193, 0.2);
    backdrop-filter: blur(10px);
}

.form-group {
    margin-bottom: 1.5rem;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 182, 193, 0.3);
    border-radius: 10px;
    color: var(--cream);
    font-family: var(--body-font);
    transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: var(--peach-pink);
    box-shadow: 0 0 10px rgba(255, 182, 193, 0.3);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
    color: rgba(253, 246, 227, 0.6);
}

/* Footer */
.footer {
    background: rgba(26, 26, 46, 0.9);
    padding: 2rem 0;
    text-align: center;
    border-top: 1px solid rgba(255, 182, 193, 0.2);
}

.footer p {
    color: var(--cream);
    opacity: 0.8;
}

/* Responsive Design */
@media (max-width: 768px) {
    .hero-container {
        grid-template-columns: 1fr;
        text-align: center;
    }
    
    .hero-title {
        font-size: 3rem;
    }
    
    .contact-content {
        grid-template-columns: 1fr;
        gap: 2rem;
        max-width: 95vw;
        min-height: unset;
    }
    .contact-left, .contact-right {
        align-items: center;
        text-align: center;
        height: unset;
    }
    .contact-left p {
        max-width: 100%;
    }
    .contact-details {
        max-width: 100%;
    }
    
    .nav-menu {
        display: none;
    }
    
    .hamburger {
        display: block;
    }
    
    .section-title {
        font-size: 2.2rem;
    }
    
    .hero-subtitle {
        font-size: 1.3rem;
    }
    
    .btn {
        font-size: 1rem;
        padding: 14px 24px;
    }
    
    .profile-card,
    .skill-category,
    .project-card,
    .contact-form,
    .stat {
        padding: 1.2rem;
    }
    
    .hero {
        padding-top: 12px;
    }
}

/* Hamburger Menu */
.hamburger {
    display: none;
    flex-direction: column;
    cursor: pointer;
}

.bar {
    width: 25px;
    height: 3px;
    background: var(--peach-pink);
    margin: 3px 0;
    transition: 0.3s;
}

/* Scroll Animations */
.fade-in {
    opacity: 0;
    transform: translateY(30px);
    transition: all 0.6s ease;
}

.fade-in.visible {
    opacity: 1;
    transform: translateY(0);
}

/* Smooth Scrolling */
html {
    scroll-behavior: smooth;
}

/* Selection Styling */
::selection {
    background: rgba(255, 182, 193, 0.3);
    color: var(--cream);
}

/* Scrollbar Styling */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: var(--midnight-blue);
}

::-webkit-scrollbar-thumb {
    background: linear-gradient(45deg, var(--peach-pink), var(--lavender));
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(45deg, var(--lavender), var(--peach-pink));
}

/* Floating Navigation Bar */
.floating-nav {
  position: fixed;
  left: 50%;
  bottom: 36px;
  transform: translateX(-50%);
  display: flex;
  gap: 2.2rem;
  background: rgba(40, 40, 60, 0.55);
  box-shadow: 0 8px 32px rgba(30, 30, 60, 0.18), 0 1.5px 8px rgba(255, 182, 193, 0.08);
  border-radius: 2.5rem;
  padding: 0.7rem 2.2rem;
  backdrop-filter: blur(16px);
  z-index: 2000;
  border: 1.5px solid rgba(255, 255, 255, 0.08);
}

.floating-nav-btn {
  background: none;
  border: none;
  outline: none;
  color: #e6e6fa;
  font-size: 1.6rem;
  padding: 0.7rem;
  border-radius: 50%;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.floating-nav-btn.active,
.floating-nav-btn:hover {
  background: linear-gradient(135deg, var(--midnight-blue), var(--peach-pink));
  color: #fff;
  box-shadow: 0 0 0 4px rgba(255, 182, 193, 0.08);
}

@media (max-width: 600px) {
  .floating-nav {
    gap: 1.2rem;
    padding: 0.5rem 1.2rem;
  }
  .floating-nav-btn {
    font-size: 1.2rem;
    padding: 0.5rem;
  }
}

/* --- Social Row Layout --- */
.social-row-layout {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  gap: 3.5rem;
  margin-top: 2.5rem;
  position: relative;
}

.social-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  min-width: 110px;
}

.social-link {
  width: 60px;
  height: 60px;
  background: linear-gradient(45deg, var(--peach-pink), var(--lavender));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--midnight-blue);
  text-decoration: none;
  font-size: 2rem;
  margin-bottom: 0.7rem;
  box-shadow: 0 2px 12px 0 rgba(179, 157, 219, 0.15);
  transition: all 0.3s ease;
}

.social-link:hover {
  transform: translateY(-3px) scale(1.1);
  box-shadow: 0 6px 24px 0 var(--peach-pink), 0 0 16px 0 var(--lavender);
}

.social-tagline {
  background: rgba(255, 255, 255, 0.08);
  color: var(--cream);
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 1.5rem;
  margin-bottom: 0.2rem;
  margin-top: 0.2rem;
  box-shadow: 0 2px 12px 0 rgba(179, 157, 219, 0.15);
  text-align: center;
  font-weight: 500;
  position: relative;
  z-index: 2;
  transition: box-shadow 0.3s, transform 0.3s;
}

.curvy-arrow-up {
  margin-top: -10px;
  margin-bottom: 0.2rem;
  width: 60px;
  height: 40px;
  filter: drop-shadow(0 0 8px #b39ddb88);
  transition: filter 0.3s, transform 0.3s;
  z-index: 1;
  pointer-events: none;
}

.social-link:hover ~ .social-tagline,
.social-link:hover ~ .curvy-arrow-up,
.social-tagline:hover,
.curvy-arrow-up:hover {
  box-shadow: 0 4px 24px 0 #ffb6c1cc;
  filter: drop-shadow(0 0 16px #ffb6c1cc) drop-shadow(0 0 8px #b39ddbcc);
  transform: scale(1.08) rotate(-2deg);
}

@media (max-width: 900px) {
  .social-row-layout {
    gap: 1.5rem;
  }
  .social-link {
    width: 48px;
    height: 48px;
    font-size: 1.5rem;
  }
}

@media (max-width: 600px) {
  .social-row-layout {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }
  .social-col {
    min-width: 0;
  }
}

@media (max-width: 900px) {
  .edu-exp-wrapper {
    flex-direction: column !important;
    gap: 2.5rem !important;
    align-items: center !important;
  }
}

.projects-grid-inner {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
  justify-content: center;
  width: 100%;
}

@media (max-width: 1100px) {
  .projects-grid-inner {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 700px) {
  .projects-grid-inner {
    grid-template-columns: 1fr;
  }
  .container {
    padding: 0 1rem;
    max-width: 100vw;
  }
  .projects-grid-inner,
  .about-content,
  .contact-content {
    width: 100%;
    max-width: 100vw;
  }
} 